import React from 'react';
import firebase from './Firebase';
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CFormGroup,
  CLabel,
  CFormText,
  CCol,
  CContainer,
  CCardHeader,
  CForm,
  CInput,
  CImg,
  CSwitch,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import './scss/style.scss';
import { icons } from './assets/icons/index.js';
import { withRouter } from 'react-router-dom';
React.icons = icons
class Jos2 extends React.Component{
  constructor(props) {
  super(props);
  this.state = {status: "Loading..",color: "secondary",action:"Loading..",colorAction:"secondary",imgurl:"",suhu:"Loading.."}
  } 
	nyalakan = () => {
  if (this.state.status == "ON") {    
	firebase.database().ref('LED1').set("0");
    } else {
  firebase.database().ref('LED1').set("1");
    }  
  }
  tambah = () => {
  if (this.state.suhu <= 29) {  
  const suhu = this.state.suhu 
  const suhudirubah = suhu + 1   
  firebase.database().ref('suhu').set(suhudirubah);
    }
  if (this.state.suhu == 16) {  
  const suhu = this.state.suhu 
  const suhudirubah = suhu + 1   
  firebase.database().ref('suhu').set(suhudirubah);
    }    
  }
  kurang = () => {
  if (this.state.suhu >= 17 && this.state.suhu <= 29) {    
  const suhu = this.state.suhu 
  const suhudirubah = suhu - 1   
  firebase.database().ref('suhu').set(suhudirubah);
    }
  if (this.state.suhu == 30) {    
  const suhu = this.state.suhu 
  const suhudirubah = suhu - 1   
  firebase.database().ref('suhu').set(suhudirubah);
    }    
  }   

  getData = () => {
  firebase.database().ref('LED1').on('value', snapshot => {
    const state = snapshot.val();
    if (state == "1") {
      this.setState({status:"AC Nyala", color:"success", action:"Matikan", colorAction:"danger", onClick:"{this.matikan.bind(this)}", imgurl:"https://firebasestorage.googleapis.com/v0/b/controlmyac.appspot.com/o/imageedit_3_2988499131.png?alt=media&token=14c78fc0-9ecc-4caa-a4a0-4e22baae080e"})
    } else {
      this.setState({status:"AC Mati", color:"danger", action:"Nyalakan", colorAction:"success", onClick:"{this.matikan.bind(this)}", imgurl:"https://firebasestorage.googleapis.com/v0/b/controlmyac.appspot.com/o/imageedit_2_2008486194.png?alt=media&token=a5f41ad0-04ce-4da6-97e7-8d0b1fc375c8"})
    }
  })
  firebase.database().ref('suhu').on('value', snapshot => {
    const suhu = snapshot.val();
      this.setState({suhu:suhu})
  })  
}
  componentDidMount = () =>{
  this.getData();
}
	render(){
		console.log('darisini' , firebase)
		return(
<div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="6">
              <CCard className="p-4">
              <CCardHeader>
              <div class="d-flex justify-content-center">
                <CImg
                src={this.state.imgurl}
                fluid
                className="mb-2"
                width="50%"
                height="auto"
                />
              </div>
              </CCardHeader>              
        <CCardBody>
          <CRow>
            <CCol sm="6" lg="6" md="6" xs="6">
              <CForm action="" method="post">                
                    <CFormGroup>
                      <CCol sm="12" lg="12" md="12" xs="12">
                      <div class="d-flex justify-content-start">
                      <CButton disabled color={this.state.color}>{this.state.status}</CButton>
                      </div>
                      </CCol>
                    </CFormGroup>
                    <CFormGroup>
                      <CCol sm="12" lg="12" md="12" xs="12">
                      <div class="d-flex justify-content-start">
                      <CButton onClick={this.nyalakan.bind(this)} color={this.state.colorAction}>{this.state.action}</CButton>
                      </div>                                           
                      </CCol>            
                    </CFormGroup>
              </CForm>
            </CCol>
            <CCol sm="6" lg="6" md="6" xs="6">
              <CForm action="" method="post">
                  <CFormGroup>
                      <CCol sm="12" lg="12" md="12" xs="12">
                      <div class="d-flex justify-content-start">
                      <CButton disabled color="primary">{this.state.suhu}</CButton><h4>Â°C</h4> 
                      </div>
                      </CCol>
                  </CFormGroup>
                  <CFormGroup>
                      <CCol sm="12" lg="12" md="12" xs="12">
                      <div class="d-flex justify-content-start">
                      <CButton onClick={this.tambah.bind(this)} color="secondary"><CIcon name={'cilCaretTop'} /></CButton> &nbsp;&nbsp;&nbsp;<CButton onClick={this.kurang.bind(this)} color="secondary"><CIcon name={'cilCaretBottom'} /></CButton>
                      </div>
                      </CCol>           
                  </CFormGroup>
              </CForm>
            </CCol>            
          </CRow>
        </CCardBody>
              </CCard>
          </CCol>
        </CRow>
      </CContainer>
    </div>			
///<button onClick={this.changevalue}>Changethis</button>
			)
	}
}
export default withRouter(Jos2);